<template>
  <div class="frontend" v-resize="onResize">
     <Layout class="layout-con">
            <sider  collapsible hide-trigger :collapsed-width="60" v-model="isCollapsed">
              <Menu active-name="search" theme="dark" width="auto" :class="menuitemClasses" @on-select="selectNavMenu">
                    <MenuItem name="search">
                        <Icon type="md-search"></Icon>
                    </MenuItem>
                    <MenuItem name="about">
                        <Icon type="md-at"></Icon>
                    </MenuItem>
                </Menu>
            </sider>
            <Layout>
                <Content>
                   <Split v-model="split.ratio" :min="splitMin" :max="splitMax">
            <div slot="left" class="split-pane">
              <CheckboxGroup  class="check-libs" v-model="components.filters">
                UI
                <Poptip v-for="item in components.libs" word-wrap trigger="hover" placement="bottom-start" width="200" :content="item.desc">
                  <Checkbox :label="item.category" :disabled="item.category === 'html5'">
                    <span>{{item.title}}</span>
                  </Checkbox>
                </Poptip>
                 
              </CheckboxGroup>
                <Input v-model="components.key"  suffix="ios-search" placeholder="Search" style="width: 100%; padding: 0 4px;" @on-enter="searchComponents"/>
                <Scroll :height="size.scrollHeight">
                <div class="search-item">
                  <div class="search-group">
                    <span>Custom</span>
                  </div>
                  <Option v-for="option in components.customs" :value="option.name" :key="option.name">
                    <Poptip word-wrap trigger="hover" placement="right" width="200" :title="option.title" :content="option.desc">
                      <span class="search-item-title">{{ option.name }}</span>
                    </Poptip>
                    <Button class="search-item-add" icon="ios-add" size="small" @click="addCustomComponent(option.name)"></Button>
                   </Option>
                </div>  
                <div class="search-item" v-for="item in components.searchResult">
                  <div class="search-group">
                    <span>{{ item.title }}</span>
                  </div>
                  <Option v-for="option in item.elements" :value="option.name" :key="option.name">
                    <Poptip word-wrap trigger="hover" placement="right" width="200" :title="option.title" :content="option.desc">
                      <span class="search-item-title">{{ option.name }}</span>
                    </Poptip>
                    <Button class="search-item-add" icon="ios-add" size="small" @click="addComponent(item.category, option.name)"></Button>
                   </Option>
                </div>
                </Scroll>
            </div>
            <div slot="right" class="split-pane">
                <div id="scene"></div>
            </div>
              </Split>
                    
                </Content>
            </Layout>
        </Layout>
    <div class="editor-button">
      <Button shape="circle" icon="md-create" v-if="scene.debug" @click="editScript" ></Button>
      <Tooltip placement="bottom" content="Load from file" :delay="1000">
        <Button shape="circle" icon="ios-open-outline" @click="loadScript"></Button>
      </Tooltip>
      <Tooltip placement="bottom" content="Save to file" :delay="1000">
        <Button shape="circle" icon="ios-code-download" @click="generateScript"></Button>
      </Tooltip>
      <Tooltip placement="bottom" content="Zoom Out" :delay="1000">
        <Button shape="circle" icon="ios-remove" @click="zoomOut"></Button>
      </Tooltip>
      <Tooltip placement="bottom" content="Zoom In" :delay="1000">
        <Button shape="circle" icon="ios-add" @click="zoomIn"></Button>
      </Tooltip>
      <Tooltip placement="bottom" content="Reset Canvas" :delay="1000">
        <Button shape="circle" icon="ios-refresh" @click="reset"></Button>
      </Tooltip>
      <Tooltip placement="bottom" content="Undo" :delay="1000">
        <Button shape="circle" icon="ios-undo-outline" :disabled="!hasUndo" @click="undo"></Button>
      </Tooltip>
      <Tooltip placement="bottom-end" content="Redo" :delay="1000">
        <Button shape="circle" icon="ios-redo-outline" :disabled="!hasRedo" @click="redo"></Button>
      </Tooltip>
    </div>
    <Drawer title="Script Editor" width="1024" :closable="false" v-model="scriptEditor.visible">
      <ButtonGroup class="code-button">
        <Button icon="md-create" @click="updateScript">Update</Button>
      </ButtonGroup>
      <codemirror ref="code" v-model="scriptEditor.code" :options="scriptEditor.options"></codemirror>
    </Drawer>
    <Modal
        title="Add Custom Element"
        v-model="dialogs.customElement.visible"
        :mask-closable="false">
      <Form ref="customElement" :model="dialogs.customElement.form" :rules="dialogs.customElement.rules">
        <FormItem prop="tagName">
            <Input type="text" v-model="dialogs.customElement.form.tagName" placeholder="TagName">
                <Icon type="ios-code" slot="prepend"></Icon>
            </Input>
        </FormItem> 
      </Form>
      <div slot="footer">
          <Button type="primary" @click="addCustomElement">Add</Button>
          <Button style="margin-left: 8px" @click="closeCustomElement">Cancel</Button>
      </div>
    </Modal>
    <Modal
        title="Edit Element"
        v-model="dialogs.editElement.visible"
        :width="800"
        :mask-closable="false">
      <Form ref="editElement" :model="dialogs.editElement.form" :rules="dialogs.editElement.rules" :label-width="80">
        <FormItem label="Name" prop="tagName">
            <Input type="text" v-model="dialogs.editElement.form.tagName" placeholder="TagName" :readonly="true"></Input>
        </FormItem> 
        <FormItem label="Category" prop="tagCategory">
            <Input type="text" v-model="dialogs.editElement.form.tagCategory" placeholder="Category" :readonly="true"></Input>
        </FormItem>
      </Form>
      <Row>
        <Col :span="8">
          <h3>Properties</h3>
          <Scroll>
             <CellGroup @on-click="handleSelectProperty">
              <Cell v-for="prop in dialogs.editElement.form.props" :title="prop.key"  :name="prop.key" :selected="prop.selected"/>
             </CellGroup> 
          </Scroll>
        </Col>
         <Col :span="16">
          <Form ref="editElementProperty" :model="dialogs.editElement.form.current" :label-width="80">
            <FormItem label="Key" prop="propKey">
                <Input type="text" v-model="dialogs.editElement.form.current.key" @on-change="handleChangePropertyName"></Input>
            </FormItem> 
            <FormItem label="Value" prop="propValue">
              <Input type="text" v-model="dialogs.editElement.form.current.value" :placeholder="dialogs.editElement.form.current.default"></Input>
            </FormItem>
            <FormItem label="DataType" prop="propDataType">
              <Select v-model="dialogs.editElement.form.current.datatype" :disabled="!dialogs.editElement.form.current.custom" @on-change="handleChangePropertyDataType" >
                <Option v-for="item in dialogs.editElement.dataTypes" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
            </FormItem>
            <FormItem>
              <Alert>
                  <template slot="desc">{{dialogs.editElement.form.current.desc}}</template>
              </Alert>
            </FormItem>
             <FormItem>
                <Button type="primary" @click="updateElementProperty">Modify</Button>
            </FormItem>
          </Form>
        </Col>
      </Row>
      <div slot="footer">
          <Button type="primary" @click="updateElement">Save</Button>
          <Button style="margin-left: 8px" @click="closeEditElement">Cancel</Button>
      </div>
    </Modal>
    <Modal
        title="Edit #Text"
        v-model="dialogs.editText.visible"
        :mask-closable="false">
      <Form ref="editText" :model="dialogs.editText.form">
        <FormItem prop="text">
            <Input type="textarea" :autosize="true" v-model="dialogs.editText.form.text" placeholder="Please enter text"/>
        </FormItem> 
      </Form>
      <div slot="footer">
          <Button type="primary" @click="updateText">Save</Button>
          <Button style="margin-left: 8px" @click="closeEditText">Cancel</Button>
      </div>
    </Modal>
     <Modal
        v-model="dialogs.about.visible"
        title="About">
        <h3>Troll <span style="fontWeight:normal;">-- An visual editor for vue(2.x)</span></h3>
        <p>Author: unique</p>
        <p>Email: ods_hla@163.com</p>
        <div slot="footer">
            <Button type="success" size="large"  @click="dialogs.about.visible=false">OK</Button>
        </div>
    </Modal>
  </div>
</template>

<style scoped>
