<script>
  import resize from 'vue-resize-directive'
  import Common from 'orion-common'

  import { codemirror } from 'vue-codemirror'
  import 'codemirror/mode/javascript/javascript'
  import 'codemirror/theme/monokai.css'
  import troll from './troll/index'
  import TEST from './test'
  import UILibs from './ui/index'

  const parse5 = require('parse5')
  const beautify = require('vue-beautify')

  export default {
    props: ["source"],
    directives: {
      resize,
    },
    components: {codemirror},
    data: function () {
      const validateName = (rule, value, callback) => {
        if (!value || value.length === 0) {
          callback(new Error('Please enter tagname'))
        } else {
          callback()
        }
      }

      return {
        split: {
          ratio: 0.3,
          oldRatio: 0.3,
          minWidth: 300,
          maxWidth: 600,
        },
        isCollapsed: true,
        size: {
          width: 0,
          height: 0,
          scrollHeight: 0,
        },
        dialogs: {
          customElement: {
            visible: false,
            parent: null,
            form: {
              tagName: ''
            },
            rules: {
              tagName: [
                  { validator: validateName, trigger: 'blur' }
              ]
            }
          },
          editElement: {
            visible: false,
            model: null,
            dataTypes: [
              {label: 'number', value: 'number'},
              {label: 'string', value: 'string'},
              {label: 'boolean', value: 'boolean'},
              {label: 'enum', value: 'enum'},
              {label: 'array', value: 'array'},
              {label: 'object', value: 'object'},
              {label: 'function', value: 'function'}
            ],
            form: {
              tagName: '',
              tagCategory: '',
              def: null,
              props: [],
              current: {
                key: '',
                value: '',
                datatype: 'string',
                default: '',
                desc: '',
                custom: true
              }
            }
          },
          editText: {
            visible: false,
            model: null,
            form: {
              text: ''
            }
          },
          about: {
            visible: false
          }
        },
        components: {
          filters: [],
          libs: [],
          customs: [
            {
              name: 'element',
              title: '自定义元素标签',
              desc: '用于定义自定义标签'
            }
          ],
          key: '',
          searchResult: []
        },
        scene: {
          stage: null,
          debug: true
        },
        scriptEditor: {
          document: null,
          code: '',
          options: {
            tabSize: 2,
            mode: 'text/javascript',
            theme: 'monokai',
            lineNumbers: true,
            line: true,
            matchBrackets: true,
            autofocus: true
          },
          visible: false
        }
      };
    },
    computed: {
      hasUndo: function () {
        return this.scene.stage ? this.scene.stage.hasUndo() : false
      },
      hasRedo: function () {
        return this.scene.stage ? this.scene.stage.hasRedo() : false
      },
      menuitemClasses: function () {
        return [
            'menu-item',
            this.isCollapsed ? 'collapsed-menu' : ''
        ]
      },
      splitMin: function () {
        return this.split.minWidth + 'px'
      },
      splitMax: function () {
        return this.split.maxWidth + 'px'
      }
    },
    methods: {
       onResize() {
        this.size.width = this.$el.clientWidth
        this.size.height = this.$el.clientHeight
        this.size.scrollHeight = this.size.height - 80
        this.scene.stage.resize(this.size.width, this.size.height)
      },
      zoomIn () {
        this.scene.stage.zoomIn()
      },
      zoomOut () {
        this.scene.stage.zoomOut()
      },
      reset () {
        this.scene.stage.reset()
      },
      undo () {
        this.scene.stage.undo()
      },
      redo () {
        this.scene.stage.redo()
      },
      editScript () {
        this.scriptEditor.visible = true
      },
      readScript(script) {

      },
      loadScript (script) {
        if (script.length === 0) {
          this.scene.stage.clear()
          return
        }
        this.scriptEditor.document = parse5.parseFragment(script)
        let template = null
        for(let child of this.scriptEditor.document.childNodes){
          if (child.tagName === 'template'){
            template = child
            break
          }
        }

        if(!template) {
           this.$Message.error('can not find template block')
        } else {
          let root = null
           for(let child of template.content.childNodes){
              if (child.nodeName !== '#text'){
                root = child
                break
              }
          }
          this.scene.stage.loadFromFile(root ? parse5.serialize(root) : '')
        }
      },
      generateScript () {
        let code = this.scene.stage.saveToFile()
        if (this.scriptEditor.document) {
          for(let child of this.scriptEditor.document.childNodes){
            if (child.tagName === 'style') {
              code += `<style>${parse5.serialize(child)}</style>`
            } else if (child.tagName === 'script') {
              code += `\<script\>${parse5.serialize(child)}</scr` + 'ipt>'
            }
          }
        }
        this.scriptEditor.code = beautify(code)
      },
      updateScript () {
        this.scriptEditor.visible = false
        let model = this.scriptEditor.code
        // 剔除注释
        //model = model.replace(/(?:^|\n|\r)\s*\/\*[\s\S]*?\*\/\s*(?:\r|\n|$)/g, '\n').replace(/(?:^|\n|\r)\s*\/\/.*(?:\r|\n|$)/g, '\n')
        //model = Common.Utils.common.trimString(model)
        this.loadScript(model)
      },
      searchComponents () {

        this.components.searchResult = UILibs.search({
          key: this.components.key,
          filters: this.components.filters
        })

        console.log(this.components.searchResult)
      },
      addComponent (category, tag, parent = null) {
        let sel = parent
        if (!sel) {
          sel = this.scene.stage.selectedElement()
        }
       
        if (!sel) {
          this.$Message.warning({
            content: 'Please select a element first'
          })
        } else {
          if (category === 'html5' && tag === '#text') {
            this.scene.stage.addText({
              text: ''
            }, sel)
          } else {
            this.scene.stage.addElement({
              tagName: tag
            }, sel)
          }
          this.scene.stage.refresh()
          this.scene.stage.snapshot()    
        }
      },
      addCustomComponent(name) {
        let sel = this.scene.stage.selectedElement()
        if (!sel) {
          this.$Message.warning({
            content: 'Please select a element first'
          })
        } else {
          if ( name === 'element') {
            this.$refs['customElement'].resetFields()
            this.dialogs.customElement.parent = sel
            this.dialogs.customElement.visible = true
          }
        }
      },
      addCustomElement() {
        this.$refs['customElement'].validate((valid) => {
          if (valid) {
            this.dialogs.customElement.visible = false
            let tagName = this.dialogs.customElement.form.tagName.toLowerCase()
            this.addComponent('custom', tagName, this.dialogs.customElement.parent)
            this.dialogs.customElement.parent = null         
          }
        })
      },
      closeCustomElement () {
        this.dialogs.customElement.visible = false
        this.dialogs.customElement.parent = null 
      },
      updateElement () {
        this.dialogs.editElement.visible = false
        // 更新元素Category
        const elem = this.dialogs.editElement.model
        elem.config.tagCategory = this.dialogs.editElement.form.tagCategory
        // 更新元素属性值
        elem.clearProperty()

        for(let p of this.dialogs.editElement.form.props) {
          if (p.selected) {
            console.log(p.key, p.value)
            elem.addProperty({
              key: p.key,
              value: p.value
            })
          }
        }
        elem.adjust()
        elem.refresh()
        this.dialogs.editElement.model = null
        this.scene.stage.snapshot()
      },
      updateElementProperty () {
        let found = false
        for(let p of this.dialogs.editElement.form.props) {
          if (p.key === this.dialogs.editElement.form.current.key){
            p.value = this.dialogs.editElement.form.current.value
            p.selected = !(p.value === '' || p.value === p.default)
            found = true
            break
          }
        }

        if (!found) {
          this.dialogs.editElement.form.props.push(Object.assign({selected: true}, this.dialogs.editElement.form.current))
        }

      },
      handleSelectProperty (name) {

        for(let p of this.dialogs.editElement.form.props) {
          if (p.key === name){
              this.dialogs.editElement.form.current.key = p.key
              this.dialogs.editElement.form.current.value = p.value
              this.dialogs.editElement.form.current.datatype = p.datatype
              this.dialogs.editElement.form.current.default = p.default
              this.dialogs.editElement.form.current.desc = p.desc
              this.dialogs.editElement.form.current.custom = p.custom
            break
          }
        }
      },
      handleChangePropertyName (evt) {
        let found = false
        for(let p of this.dialogs.editElement.form.props) {
          if (p.key === this.dialogs.editElement.form.current.key){
            found = true
            break
          }
        }
        
        // 调整为自定义属性
        if (!found) {
          this.dialogs.editElement.form.current.custom = true
          this.dialogs.editElement.form.current.desc = '自定义属性'
        }
      },
      handleChangePropertyDataType (name) {
        if (name === 'array') {
          this.dialogs.editElement.form.current.default = '[]'
        } else if (name === 'boolean') {
          this.dialogs.editElement.form.current.default = 'false'
        } else if (name === 'number') {
          this.dialogs.editElement.form.current.default = '0'
        } else {
          this.dialogs.editElement.form.current.default = ''
        }
      },
      closeEditElement () {
        this.dialogs.editElement.visible = false
        this.dialogs.editElement.model = null 
      },
      updateText () {
        this.dialogs.editText.visible = false
        this.dialogs.editText.model.changeText(this.dialogs.editText.form.text)
        this.dialogs.editText.model.refresh()
        this.dialogs.editText.model = null
        this.scene.stage.snapshot()
      },
      closeEditText () {
        this.dialogs.editText.visible = false
        this.dialogs.editText.model = null 
      },
      selectNavMenu (name) {
        if (name === 'search') {
          if(this.split.ratio < 0.1) {
            this.split.ratio = this.split.oldRatio
            this.split.minWidth = 300
            this.split.maxWidth = 600
          } else {
            this.split.oldRatio = this.split.ratio
            this.split.ratio = 0
            this.split.minWidth = 0
            this.split.maxWidth = 1
          }
        } else if (name === 'about') {
          this.dialogs.about.visible = true
        }
      },
      test(){        
        let model = '<template><div class="container" v-resize="onContainerResize"><Layout><Sider hide-trigger width="300"><Tree ref="tree" :data="tree.data" class="card-tree"></Tree></Sider><Content><table></Table></Content></Layout></div></template>\<s></s><abc></abc>'
        const document = parse5.parseFragment(model)
        let template = null
        for(let child of document.childNodes){
          if (child.tagName === 'template'){
            template = child
            break
          }
        }

        if(!template) {
           this.$Message.error('can not find template block')
        } else {
          this.scene.stage.loadFromFile(template.content ? parse5.serialize(template.content) : '')
        }
      }
    },
    created: function () {
      this.components.libs = UILibs.getCategories()
      for(let lib of this.components.libs){
        this.components.filters.push(lib.category)
      }
    },
    mounted: function () {
      this.size.width = this.$el.clientWidth
      this.size.height = this.$el.clientHeight
      this.size.scrollHeight = this.size.height - 80

      this.scene.stage = troll.init({
        container: 'scene',
        width: this.size.width,
        height: this.size.height,
        debug: true,
        events: {
          select: (elem) => {

          },
          edit: (elem, prop) => {
            const nodeType = elem.nodeType()
            if (nodeType === 'element') {
              this.dialogs.editElement.form.tagName = elem.tagName()
              this.dialogs.editElement.form.tagCategory = elem.tagCategory() || ''

              let defs = UILibs.search({
                key: this.dialogs.editElement.form.tagName,
                fuzzy: false,
                filters: (this.dialogs.editElement.form.tagCategory !== '') ? [this.dialogs.editElement.form.tagCategory] : []
              })

              const showDlg = () => {
                // 初始化props属性
                let props = []
                let maps = {}
                const form = this.dialogs.editElement.form
                let propdefs = {}
                if (form.def && form.def.elements && form.def.elements.length >= 1) {
                  propdefs = form.def.elements[0].props
                }

                let elemjson = elem.toJson()
                for(let p of elemjson.properties){
                  if (propdefs[p.config.key]){
                    maps[p.config.key] = true
                    const pdef = propdefs[p.config.key]
                    props.push({
                      key: p.config.key,
                      value: p.config.value || '',
                      desc: pdef.desc || p.config.key,
                      datatype: pdef.datatype || 'string',
                      default: pdef.default || '',
                      selected: true,
                      custom: false
                    })
                  } else {
                    props.push({
                      key: p.config.key,
                      value: p.config.value || '',
                      desc: '自定义属性',
                      datatype: 'string',
                      default: '',
                      selected: true,
                      custom: true
                    })
                  }
                }
                
                for(let [k,v] of Object.entries(propdefs)) {
                  if (!maps[k]) {
                    props.push({
                      key: k,
                      value: '',
                      desc: v.desc || k,
                      datatype: v.datatype || 'string',
                      default: v.default || '',
                      selected: false,
                      custom: false
                    })
                  }
                }
                this.dialogs.editElement.form.props = props
                this.dialogs.editElement.model = elem
                this.dialogs.editElement.visible = true
              }

              // 如果找到多个，则需要进行选择
              if (defs.length > 1) {
                this.dialogs.editElement.form.tagCategory = defs[0].category

                this.$Modal.confirm({
                    title: 'Select Component Library',
                    render: (h) => {
                        return h('i-select', {
                            props: {
                                value: this.dialogs.editElement.form.tagCategory
                            },
                            on: {
                                'on-change': (val) => {
                                  this.dialogs.editElement.form.tagCategory = val
                                }
                            }
                        }, 
                        defs.map((item) => {
                          return h('i-option', {
                            props: {
                              value: item.category,
                              label: item.title
                            }
                          })
                       }))
                    },
                    onOk: () => {
                      for(let d of defs) {
                        if (d.category === this.dialogs.editElement.form.tagCategory) {
                          this.dialogs.editElement.form.def = d
                          break
                        }
                      }
                      showDlg()
                    }
                })
              } else if (defs.length === 1) {
                this.dialogs.editElement.form.def = defs[0]
                this.dialogs.editElement.form.tagCategory = this.dialogs.editElement.form.def.category
                showDlg()
              } else {
                this.dialogs.editElement.form.tagCategory = 'custom'
                this.dialogs.editElement.form.def = null
                showDlg()
              }
              
            } else if (nodeType === 'text') {
              // 正文内容
              this.dialogs.editText.model = elem
              this.dialogs.editText.form.text = elem.code()
              this.dialogs.editText.visible = true
            }
          }
        }
      })

      //this.test()

      //window.__callVscode__({ cmd: 'alert', info: 'info' })
      window.__callVscode__({ cmd: 'loadfile', info: '' }, (data) => {
        this.loadScript(data.content || '')
      })

    },
    activated: function () {
    }
  };
</script>

